import React, { useState, useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Nav from 'react-bootstrap/Nav';
import LogoutButton from '../auth/Logout';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';

function AdminPage() {

    const [tables, setTables] = useState([]); // Stores table list
    const [newTableName, setNewTableName] = useState('');
    
    const fetchTables = async () => {
        try {
            const response = await fetch('http://localhost:5000/api/tables');
            const data = await response.json();
            setTables(data.tables);
        } catch (error) {
            console.error('Error fetching tables:', error);
        }
    };
    
    // Add a new table
    const addTable = async () => {
        if (!newTableName.trim()) {
            alert('Table name is required');
            return;
        }
    
        try {
            const response = await fetch('http://localhost:5000/api/tables', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ name: newTableName }),
            });
    
            if (response.ok) {
                const data = await response.json();
                setTables(data.tables);
                setNewTableName('');
                alert('Table added successfully');
            } else {
                const error = await response.json();
                alert(`Error: ${error.error}`);
            }
        } catch (error) {
            console.error('Error adding table:', error);
        }
    };
    
    // Remove a table
    const removeTable = async (tableName) => {
        try {
            const response = await fetch(`http://localhost:5000/api/tables/${tableName}`, {
                method: 'DELETE',
            });
    
            if (response.ok) {
                const data = await response.json();
                setTables(data.tables);
                alert('Table removed successfully');
            } else {
                const error = await response.json();
                alert(`Error: ${error.error}`);
            }
        } catch (error) {
            console.error('Error removing table:', error);
        }
    };
    
    useEffect(() => {
        fetchTables();
    }, []);

    return (
        <Container>
            <Row>
                <Col>
                    <h1>Admin Page</h1>
                    <div
                        style={{
                            display: 'flex', // Enables horizontal layout
                            alignItems: 'flex-start', // Aligns items at the top
                            gap: '20px', // Adds space between Nav and content
                        }}
                    >
                        {/* Nav Div */}
                        <Nav
                            defaultActiveKey="/home"
                            className="flex-column"
                            style={{
                                backgroundColor: 'lightblue',
                                padding: '20px 20px 540px',
                                borderRadius: '30px',
                                width: '130px', // Fixed width for consistent layout
                            }}
                        >
                            <Nav.Link
                                href="/register"
                                style={{
                                    padding: '10px',
                                    borderRadius: '5px',
                                    color: 'black',
                                    textDecoration: 'none',
                                    backgroundColor: 'white',
                                    marginBottom: '10px',
                                    borderColor: 'white',
                                }}
                            >
                                Register
                            </Nav.Link>
                            <LogoutButton />
                        </Nav>

                        {/* Notes and Calendar Divs */}
                        <div
                            style={{
                                display: 'flex',
                                flexDirection: 'column', // Stacks Notes and Calendar vertically
                                flex: 1, // Ensures it takes up remaining space
                                gap: '20px', // Adds space between Notes and Calendar
                            }}
                        >
                            {/* Notes Div */}
                            <div
                                style={{
                                    backgroundColor: 'lightblue',
                                    padding: '40px',
                                    borderRadius: '30px',
                                    textAlign: 'center',
                                    width: '100%',
                                    maxWidth: '1000px', // Optional: Limits max width
                                }}
                                >
                                <p>Notes/Announcements</p>
                            </div>

                            
                            <div>
                        <h2>Manage Database Tables</h2>
                        {/* Add Table Section */}
                        <div style={{ marginBottom: '20px' }}>
                            <input
                                type="text"
                                placeholder="Enter table name"
                                value={newTableName}
                                onChange={(e) => setNewTableName(e.target.value)}
                            />
                            <Button onClick={addTable} style={{ marginLeft: '10px' }}>
                                Add Table
                            </Button>
                        </div>
                        {/* Table List Section */}
                        <Table striped bordered hover>
                            <thead>
                                <tr>
                                    <th>Table Name</th>
                                    <th>Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {tables.length > 0 ? (
                                    tables.map((table) => (
                                        <tr key={table}>
                                            <td>{table}</td>
                                            <td>
                                                <Button
                                                    variant="danger"
                                                    onClick={() => removeTable(table)}
                                                >
                                                    Remove
                                                </Button>
                                            </td>
                                        </tr>
                                    ))
                                ) : (
                                    <tr>
                                        <td colSpan="2" style={{ textAlign: 'center' }}>
                                            No tables available
                                        </td>
                                    </tr>
                                )}
                            </tbody>
                        </Table>
                    </div>      

                            <div
                                style={{
                                    backgroundColor: 'lightblue',
                                    padding: '10px',
                                    borderRadius: '30px',
                                    textAlign: 'left',
                                    width: '100%',
                                    maxWidth: '1000px', // Optional: Limits max width
                                }}
                            >
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>
        </Container>

    )
}


export default AdminPage;